import colors = require('colors');
import glob = require('glob');
import fs = require('fs');
import * as path from 'path';

import IXml from '../models/packageXml/IXml';

/** Loads a directory of files into IXml objects */
export function getFiles(cwd: string, globs: string, foundMessage: string = 'Found file: '): Promise<IXml[]> {
  return new Promise<IXml[]>((complete: (files: IXml[]) => void, reject: (error: Error) => void) => {
    glob(globs, {
      cwd: cwd
    }, (error: Error, files: string[]) => {
        if (error) {
          reject(error);
        }
        complete(files.map( (filename: string): IXml => {
          console.log(foundMessage + filename);
          return {
            filename: filename,
            xml: fs.readFileSync(path.join(cwd, filename), {
              encoding: 'utf-8'
            })
          };
        }));
      });
  });
}

/** Reads the list of relative path from a specified folder */
export function getRelativeFilePaths(cwd: string, globs: string, foundMessage: string = 'Found file: '):
  Promise<string[]> {
  return new Promise<string[]>((complete: (files: string[]) => void, reject: (error: Error) => void) => {
    glob(globs, {
      cwd: cwd
    }, (error: Error, files: string[]) => {
      if (error) {
        reject(error);
      }
      complete(files.map((filename: string) => {
        filename = filename;
        console.log(colors.cyan(foundMessage + filename));
        return filename;
      }));
    });
  });
}